* {
    margin: 0;
    padding: 0;
}
html {
    font-size: 16px;
}
body {
    font-family: 'ProximaNova';
    background-color: $bg-color;
    color: $color;
    line-height: 1.3;
}
section {
    margin-bottom: $section-margin;
}

h1, h2, h3, h4, h5, h6 {
font-weight: 700;
}
h1 {
font-size: 56px;
line-height: 1.26;
}
h2 {
font-size: 36px;
line-height: 1.245;
}
h3 {
font-size: 22px;
line-height: 1.245;
}
h4 {
font-size: 20px;
line-height: 1.355;
}
h5 {
font-size: 18px;
line-height: 1.245;
}
h6 {
font-size: 17px;
line-height: 1.26;
}

.fw-300 {
    font-weight: 300;
}
.fw-400 {
    font-weight: 400;
}
.fw-600 {
    font-weight: 600;
}
.fw-700 {
    font-weight: 700;
}
.btn {
    font-size: 15px;
    background-color: $primary-color;
    color: $color;
    border-color: $primary-color;
    border-radius: 90px;
    padding: 12px 24px;
    &:hover {
        background-color: transparent;
        border-color: $primary-color;
    }
    &.btn-sm {
       padding: 14px 21px; 
    }
    &.btn-lg {
        font-size: 16px;
        padding: 20px 23px 19px 33px;
        img {
            margin-left: 49px;
        }
    }
    &.btn-secondary {
        background-color: transparent;
        &:hover {
            background-color: $primary-color;
        }
    }
    &.btn-primary {
        background-color: $primary-color;
        &:hover {
            background-color: $primary-color;
}
}
}
.template-title {
    text-align: center;
    position: relative;
    padding-bottom: 70px;

    h2 { 
        text-transform: uppercase;
        margin-bottom: 12px;

    &:after {
        content: "";
        position: absolute;
        width: 85px;
        height: 2px;
        background-color: #E58D35;
        bottom: 54px;
        left: 50%;
        transform: translateX(-50%);
    }}
    h3 {
        font-size: 24px;
        font-weight: 400;
    }
    &.template-title_case-2 {
        text-align: left;
        h2 {
            &:after {
                left: 0;
                transform: none;
            }
        }
    }
     &.template-title_case-3 {
        text-align: left;
        max-width: 626px;
        
        h2 {
            &:after {
               position: static;
               display: inline-block;
               transform: none;
               margin: 0 0 8px 20px;
            }
        }
    }
}