.calculation-form {
    max-width: 626px;
    height: 597px;
    background: $bg-color-accent;
    border-radius: 20px;
    padding:  50px 50px 60px;
}
.form-row {
    display: flex;
    flex-wrap: wrap;
    gap: 28px 30px;
    margin-bottom: 34px;
     &:last-of-type {
        margin-bottom: 24px;
     }
}
.form-column {
    width: calc(50% - 15px);
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.input-box {
    display: flex;
    flex-direction: column;
    position: relative;
}
.input-box_checkbox {
    padding-left: 33px;
    padding-top: 2px;
}
input {
    background-color: $color;
    font-size: 15px;
    border-radius: 10px;
    padding: 18px 20px;

    &:focus {

    }
    &:focus-visible {
     outline: none;
    }
    &:hover {

    }
     &:active {

    }
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance:textfield;
}

input[type='checkbox'] {
    display: none;
    + label {
        &:hover {
            &:before {
                border-color: $primary-color;
            }
        }
    }
}
input[type='checkbox'] + label:before {
    content: "";
    background-color: $color;
    border: 1px solid $bg-color;
    position: absolute;
    width: 24px;
    height: 24px;
    top: 0;
    left: 0;
    border-radius: 4px;
}
input[type='checkbox']:checked + label:before {
    content: "";
    background: url(./assets/img/icon-checked.png) center no-repeat #fff;

}